<template>
  <div v-if="solution.length > 0">
    <div class="pb-10" v-if="!unknown">
      <div class="py-4 text-center">
        <h1 class="text-2xl font-bold dark:text-white poppins">
          Solution for Exam - {{ this.$route.params.id }}
        </h1>
      </div>
      <div class="mx-2 md:w-2/3 md:mx-auto">
        <div
          v-for="(q, i) in solution"
          :key="i"
          class="p-5 mx-auto my-3 text-lg border border-gray-300 rounded-md shadow-lg  bg-gray-50 dark:bg-black dark:text-white kalpurush"
        >
          <div v-html="q.question"></div>
          <div class="mt-3">
            <div
              v-html="q.a"
              v-if="q.a"
              :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold text-black  my-3 p-3 rounded  ${
                q.a == q.correct
                  ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                  : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
              }`"
            ></div>
            <div
              v-html="q.b"
              v-if="q.b"
              :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                q.b == q.correct
                  ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                  : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
              }`"
            ></div>
            <div
              v-html="q.c"
              v-if="q.c"
              :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                q.c == q.correct
                  ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                  : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
              }`"
            ></div>
            <div
              v-html="q.d"
              v-if="q.d"
              :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                q.d == q.correct
                  ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                  : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
              }`"
            ></div>
          </div>
          <h2 v-if="q.explain" class="my-2 poppins">Explain:</h2>
          <div
            class="p-3 my-3 bg-gray-200 border border-green-500 rounded  dark:bg-gray-600"
            v-if="q.explain"
            v-html="q.explain"
          ></div>
        </div>
      </div>
    </div>
    <div v-else>
      <h2 class="mt-10 text-2xl text-center dark:text-white">
        You must participate in the exam or you will not be able to view this
        solution untill time ends.
      </h2>
    </div>
  </div>

  <div v-else class="flex items-center justify-center w-full h-screen">
    <div
      class="flex items-center justify-center -mt-24 space-x-1 text-sm text-gray-700  dark:text-white"
    >
      <svg
        fill="none"
        class="w-10 h-10 animate-spin"
        viewBox="0 0 32 32"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          clip-rule="evenodd"
          d="M15.165 8.53a.5.5 0 01-.404.58A7 7 0 1023 16a.5.5 0 011 0 8 8 0 11-9.416-7.874.5.5 0 01.58.404z"
          fill="currentColor"
          fill-rule="evenodd"
        />
      </svg>

      <div class="text-lg">Loading ...</div>
    </div>
  </div>
</template>
<script>
import { exams } from "@/plugins/auth.js";
export default {
  data() {
    return {
      solution: [],
      loading: false,
      unknown: false,
    };
  },
  beforeRouteEnter(to, from, next) {
    if (to.query.e < Date.now() && to.query.d === "m") {
      next();
      return;
    }
    next({
      path: "/",
    });
  },
  methods: {
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    getQuestionLink() {
      this.loading = true;
      this.url = exams + atob(this.$route.query.w);

      fetch(this.url + "&type=question")
        .then((r) => r.json())
        .then((d) => {
          this.solution = d;
          this.loading = false;
        });
    },
    startRetake() {
      this.retake = true;
      this.showRemaining(Date.now() + 60000 * this.end);
    },
    retakeResult() {
      let score = this.solution.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);
      this.$swal({
        icon: "success",
        title: "Success",
        text: `You Scored ${score}`,
      }).then(() => {
        this.$router.replace(
          `/ranking/${this.$route.params.type}/${this.$route.params.id}`
        );
      });
    },
  },

  created() {
    this.getQuestionLink();
  },
};
</script>

<style lang="scss" scoped>
.fixed__timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  color: white;
  font-size: 22px;
  position: fixed;
  bottom: 0;
}

.fixed__timer > div {
  font-size: 20px;

  padding: 10px;

  border-radius: 5px;
}

.solution {
  border: 1px solid rgba(black, 0.2);
  border-radius: 4px !important;
  background: linear-gradient(45deg, #f7f4f4, #d8d6d6b4) !important;
  box-shadow: 2px 2px 2px #c2c0c0, -2px -2px 4px #ffffff !important;

  div {
    color: black;
  }

  p {
    color: black;
  }
}

.examcontainer {
  background: rgba(255, 255, 255, 0.09) !important;
  backdrop-filter: blur(5px);
}

.examcontainerq {
  background: rgba(255, 255, 255, 0.774) !important;
  backdrop-filter: blur(5px);
}
</style>